	module.exports = {"data":{"markdownRemark":{"html":"<p>初の海外サーバ、<a href=\"http://www.virtualave.net/\">VirtualAvenue</a>に移転し、共にまたもリニューアル。(ミラーサイトとして HOOPS→サービス終了も使用していた) 受験勉強に飽きて(<a href=\"/2957\">だから滑ったんだなｗ</a>)またグラフィックに凝り始める。  今まではフルカラーグラフィック作成に “<a href=\"http://www10.plala.or.jp/kiriman/\">HyperPaint</a>” を使っていたが、この辺から2005年頃に至るまで “<a href=\"http://www.pixia.jp/\">PIXIA</a>” を使い始める。 <img src=\"https://danmaq.com/wp-content/uploads/1999/09/yasim3.jpg\"> <img src=\"https://danmaq.com/wp-content/uploads/1999/09/yasim4.jpg\" alt=\"yasim4\"> 背景色赤茶(後期はロゴを一新して青緑ベース)。当時、 beatmania でお馴染みの Hiroshi Watanabe 氏のサイトデザインに憧れて真似なども結構やってた。特にスクリプトを併用した飛び出し式リストボックスがリンクになっているやつが気に入っていた。 この辺まで、 HTML 構築に Macromedia (現 Adobe) の Dreamwaver を使用していた。2000 年辺りを目途に、以降ずっとテキストエディタのみで HTML を書くようになる。</p>","frontmatter":{"cover":null,"date":"1999-09-24T00:00:00+09:00","strDate":"1999/9/23","title":"YASiM-CITY"}}},"pathContext":{"path":"/ja/blog/1999/09/24-yasim-city/","langKey":"ja"}}

